}


.imgstyle_criarconta {
    width: 45px;
    margin-top: 20px;
    position: absolute;
    right: 40.8%;
    top: 10.5%;
}

.cabecalhostyle {
    text-align: center;
    margin: 50px;
    height: 150px;
    /* border: 1px solid rgb(155, 59, 59); */
    background-color: #0f1011;
    border-radius: 8px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-right: 50px;

}

.buttonentrarstyle {
    border-radius: 30px;
    width: 70px;
    height: 30px;
    background-color: yellow;
    color: rgb(0, 0, 0);
    position: absolute;
    bottom: 24%;
    left: 57%;
}

.containerbuttonentrar {
    margin-top: 40px;
    margin-left: 300px;

}

.containerfaçalogin {
    text-align: center;
    font-size: larger;
    margin: 50px;
    padding: 10px;
    color: white;
}

.containerinputlogin {
    width: 500px;
    height: 650px;
    text-align: center;
    margin-left: 720px;
    background-color: #1c1d20;
    border-radius: 10px;

}

.containerinputmargin {
    align-items: center;

}

.container {
    background-color: #282c34;
    width: 100%;
    height: 100%;
    margin: 0%;
    padding: 0%;
    position: fixed;
    background-repeat: no-repeat;
    font-family: Georgia, 'Times New Roman', Times, serif;
}


.styleborderinput {
    width: 450px;
    height: 300px;
    border-radius: 10px;
    background-color: #2f3136;
    margin-left: 25px;
    /* border: 1px solid red; */
}


.styleinputs {
    border-radius: 5px;
    background-color: #282c34;
    color: white;
    margin-left: 10px;
}

.stylelabel {
    color: yellow;
    font-size: small;
    font-size: 15px;

}


.App-logo {
    height: 40vmin;
    pointer-events: none;
}


.container_esquecisenha_criarconta {
    width: 500px;
    height: 50px;
    display: flex;
    margin-top: 5px;
}

.style_esquecisenha {

    position: absolute;
    left: 37.5%;
    font-size: 0.75em;
}

.style_criarconta {

    position: absolute;
    right: 39%;
    font-size: 0.75em;
}



/* Dispositivos móveis */
@media (min-width: 360px) and (max-width : 500px) and (min-height : 700px) and (max-height: 950px) {
