        width: 50%;
        padding: 8px;
    }

    .buttonstyle {
        width: 100%;
        margin: 5px 0;
    }


    .styleborderinput {
        width: 330px;
    }

    .buttonentrarstyle {
        margin-top: 15px;
        margin-right: 150px;
        margin-right: 260px;
