    }

    .containerbuttonentrar {
        margin-right: 150px;
        margin-top: 52px;
        margin-left: 230px;
    }

    .containerparagraforedes {
        width: 350px;
        height: auto;
        margin-left: 60px;
        margin-top: 30px;

    }

    .buttonstyle {
        width: 100%;
        margin: 0px 0;
    }


    .popupsstyle {
        width: 20px;
        height: 20px;
    }

    .containerbuttonsstyle {
        width: 120px;
        display: flex;
        margin-left: 135px;
        margin-bottom: 0px;
    }

    .container_esquecisenha_criarconta {
        /* border: 1px solid pink; */
        width: 375px;
        height: 45px;
